<template>
  <div class="table-page">
    <el-dialog title="存款" :visible.sync="dialogFormVisibleAdd" width="30%">
      <el-form width='50%'>
        <el-form-item label="存款金额">
          <el-input v-model="dailogDataAddAndDelete.balance"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleAdd = false">取 消</el-button>
        <el-button type="primary" @click="deposit">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="取款" :visible.sync="dialogFormVisibleDelete" width="30%">
    <el-form width='50%'>
        <el-form-item label="取款金额">
          <el-input v-model="dailogDataAddAndDelete.balance"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleDelete = false">取 消</el-button>
        <el-button type="primary" @click="withdraw">确 定</el-button>
      </div>
    </el-dialog>
    
    <el-dialog title="转账" :visible.sync="dailogtranfer" width="30%">
    <el-form width='50%'>
        <el-form-item label="取款金额">
          <el-input v-model="dailogDataAddAndDelete.balance"></el-input>
        </el-form-item>
        <el-form-item label="转账对象">
          <el-input v-model="dailogDataAddAndDelete.transferID"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dailogtranfer = false">取 消</el-button>
        <el-button type="primary" @click="transfer">确 定</el-button>
      </div>
    </el-dialog>
    <!--region table 表格-->
    <!-- <cm-table
      :list="list"
      :total="total"
      :maxHeight="maxHeight"
      :options="options"
      :pagination="pagination"
      :columns="columns"
      :operates="operates"
      @handleSizeChange="handleSizeChange"
      @handleIndexChange="handleIndexChange"
      @handleSelectionChange="handleSelectionChange"
    ></cm-table> -->
    <!--endregion-->
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="accountID"
        label="AccountId"
        >
      </el-table-column>
      <el-table-column
        prop="clientID"
        label="所属客户"
        >
      </el-table-column>
      <el-table-column
        prop="balance"
        label="余额">
      </el-table-column>
      <el-table-column
        prop="_id"
        label="账号唯一标识">
      </el-table-column>
      <el-table-column
        label="操作">
        <template slot-scope="scope">
          <el-button type="success" icon="el-icon-circle-plus-outline" @click="clickAddOrDelete(scope.row,1)">存款</el-button>
          <el-button type="primary" icon="el-icon-remove-outline"  @click="clickAddOrDelete(scope.row,2)">取款</el-button>
          <el-button type="danger" icon="el-icon-sort"  @click="clickTransfer(scope.row)">转账</el-button>
          <!-- <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.date }}</span> -->
        </template>

      </el-table-column>

    </el-table>
  </div>
</template>
<script>
import localStore from 'storejs'

export default {
  name: 'TestTable',
  data() {
    return {
      dailogDataAddAndDelete:{
        accountID:'',
        balance:'',
        transferID:''
      },
      dailogtranfer:false,
      dialogFormVisibleAdd:false,
      dialogFormVisibleDelete:false,
      tableData:[],
      total: 0,
      list: [
        {
          id: 1,
          title: '测试标题',
          state: 0,
          author: '测试作者',
          phone: '123456789',
          email: '51698@1564.com',
          createDate: '2019-04-21'
        },
        {
          id: 1,
          title: '测试标题',
          state: 1,
          author: '测试作者',
          phone: '123456789',
          email: '51698@1564.com',
          createDate: '2019-04-21'
        }
      ],
      maxHeight: 500,
      columns: [
        {
          prop: 'id',
          label: '编号',
          align: 'center',
          width: 60
        },
        {
          prop: 'title',
          label: '标题',
          align: 'center',
          width: 200,
          render: (h, params) => {
            return h('el-button', {
              props: { type: 'text' },
              style: { color: 'red' }
            }, params.row.title)
          }
        },
        {
          prop: 'state',
          label: '状态',
          align: 'center',
          width: '160',
          render: (h, params) => {
            return h('el-tag', {
              props: { type: params.row.state === 0 ? 'success' : params.row.state === 1 ? 'info' : 'danger' } // 组件的props
            }, params.row.state === 0 ? '上架' : params.row.state === 1 ? '下架' : '审核中')
          },
          filters: [
            {
              text: '上架',
              value: 0
            },
            {
              text: '下架',
              value: 1
            }
          ],
          sortable: true
        },
        {
          prop: 'author',
          label: '作者',
          align: 'center',
          width: 120
        },
        {
          prop: 'phone',
          label: '联系方式',
          align: 'center',
          width: 160
        },
        {
          prop: 'email',
          label: '邮箱',
          align: 'center',
          width: 240
        },
        {
          prop: 'createDate',
          label: '发布时间',
          align: 'center',
          width: 180
        }
      ], // 需要展示的列
      operates: {
        width: 200,
        fixed: 'right',
        list: [
          {
            label: '编辑',
            type: 'warning',
            show: (index, row) => {
              return true
            },
            icon: 'el-icon-edit',
            plain: true,
            disabled: false,
            method: (index, row) => {
              this.handleEdit(index, row)
            }
          },
          {
            label: '删除',
            type: 'danger',
            icon: 'el-icon-delete',
            show: true,
            plain: false,
            disabled: (index, row) => {
              return false
            },
            method: (index, row) => {
              this.handleDel(index, row)
            }
          }
        ]
      }, // 操作按钮组
      pagination: {
        pageIndex: 1,
        pageSize: 20,
      }, // 分页参数
      options: {
        stripe: true, // 是否为斑马纹 table
        loading: false, // 是否添加表格loading加载动画
        highlightCurrentRow: true, // 是否支持当前行高亮显示
        mutiSelect: true // 是否支持列表项选中功能
      } // table 的参数
    }
  },
  mounted(){
    if(!this.$store.state.user.userInfo.name){
      let tempquery=localStore.get('userInfo')
      this.$store.commit('set_userInfo',tempquery)
    }
    this.getTabledata()
    // console.log(this.clientInfo)
  },
  methods: {
    getTabledata(){
      if (this.$store.state.user.userInfo.name==='admin'){
        this.api.getAccountAll().then((res)=>{
          this.tableData=res
        })
      }else{
        this.api.getAccountById(this.$store.state.user.userInfo).then((res)=>{
          this.tableData=res
        })
      }   
    },
    clickTransfer(tempdata){
      // console.log(tempdata)
      this.dailogtranfer=true
      this.dailogDataAddAndDelete.balance=''
      this.dailogDataAddAndDelete.accountID=tempdata.accountID
    },
    clickAddOrDelete(tempdata,type){
      console.log(tempdata)
      this.dailogDataAddAndDelete.balance=''
      this.dailogDataAddAndDelete.accountID=tempdata.accountID
      if(type===1){
        this.dialogFormVisibleAdd=true
      }else{
        this.dialogFormVisibleDelete=true

      }
    },
    transfer(){
      if(this.dailogDataAddAndDelete.accountID===this.dailogDataAddAndDelete.transferID){
        this.$message.error('转出转入账号相同')
      }else{
        
        let tempwithdraw={
          accountID:this.dailogDataAddAndDelete.accountID,
          balance:this.dailogDataAddAndDelete.balance,
        }
        let tempdeposit={
          accountID:this.dailogDataAddAndDelete.transferID,
          balance:this.dailogDataAddAndDelete.balance,
        }
        // 减
        this.api.postAccountWithdraw(tempwithdraw).then((res)=>{
          // 加
          this.api.postAccountDeposit(tempdeposit).then((res)=>{
            this.dailogtranfer=false
            this.getTabledata()
            this.$message({
              message: '转账',
              type: 'success'
            })
          })
        })

        // 记录转账
        let tempquery={
          accountID:this.dailogDataAddAndDelete.accountID,
          receiver:this.dailogDataAddAndDelete.transferID,
          amount:this.dailogDataAddAndDelete.balance
        }
        this.api.postRecord(tempquery).then((res)=>{
          this.$message({
            message: '存款记录成功',
            type: 'success'
          })
        })

      }
    },
    deposit(){
      this.api.postAccountDeposit(this.dailogDataAddAndDelete).then((res)=>{
        this.dialogFormVisibleAdd=false
        this.getTabledata()
        this.$message({
          message: '存款成功',
          type: 'success'
        })
      })
      // 记录存款
      let tempquery={
        accountID:'0',
        receiver:this.dailogDataAddAndDelete.accountID,
        amount:this.dailogDataAddAndDelete.balance
      }
      this.api.postRecord(tempquery).then((res)=>{
        this.$message({
          message: '存款记录成功',
          type: 'success'
        })
      })
    },
    withdraw(){
      this.api.postAccountWithdraw(this.dailogDataAddAndDelete).then((res)=>{
        this.getTabledata()
        this.dialogFormVisibleDelete=false
        this.$message({
          message: '取款成功',
          type: 'success'
        })
      })
      // 记录取款
      let tempquery={
        receiver:'0',
        accountID:this.dailogDataAddAndDelete.accountID,
        amount:this.dailogDataAddAndDelete.balance
      }
      this.api.postRecord(tempquery).then((res)=>{
        this.$message({
          message: '取款记录成功',
          type: 'success'
        })
      })
    },    // // 切换每页显示的数量
    // handleSizeChange(pagination) {
    //   this.pagination = pagination
    //   console.log(this.pagination)
    // },
    // // 切换页码
    // handleIndexChange(pagination) {
    //   this.pagination = pagination
    //   console.log(this.pagination)
    // },
    // // 选中行
    // handleSelectionChange(val) {
    //   console.log('val:', val)
    // },
    // // 编辑
    // handleEdit(index, row) {
    //   console.log(' index:', index)
    //   console.log(' row:', row)
    // },
    // // 删除
    // handleDel(index, row) {
    //   console.log(' index:', index)
    //   console.log(' row:', row)
    // }
  }
}
</script>